.admin-panel {
  padding: 40px 0;
  min-height: 80vh;
}

.admin-header {
  background: linear-gradient(145deg, #2a2a2a 0%, #1a1a1a 100%);
  border: 2px solid rgba(211, 47, 47, 0.3);
  border-radius: 16px;
  padding: 30px;
  margin-bottom: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.4),
    inset 0 1px 0 rgba(211, 47, 47, 0.1);
}

.admin-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: linear-gradient(90deg, #d32f2f 0%, #ff6b6b 100%);
  border-radius: 16px 16px 0 0;
}

.admin-user-info h1 {
  color: #ffffff;
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 8px;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.8);
}

.user-welcome {
  color: #cccccc;
  font-size: 16px;
  display: flex;
  align-items: center;
  gap: 12px;
}

.user-welcome strong {
  color: #ffffff;
}

.admin-badge {
  background: linear-gradient(145deg, #d32f2f 0%, #b71c1c 100%);
  color: #ffffff;
  padding: 4px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.logout-button {
  background: linear-gradient(145deg, #424242 0%, #212121 100%);
  border: 2px solid rgba(211, 47, 47, 0.3);
  border-radius: 10px;
  color: #ffffff;
  padding: 12px 24px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.logout-button:hover {
  background: linear-gradient(145deg, #616161 0%, #424242 100%);
  border-color: rgba(211, 47, 47, 0.5);
  transform: translateY(-2px);
}

.admin-content {
  display: flex;
  gap: 30px;
  align-items: flex-start;
}

.admin-nav {
  display: flex;
  flex-direction: column;
  gap: 8px;
  min-width: 250px;
  background: linear-gradient(145deg, #2a2a2a 0%, #1a1a1a 100%);
  border: 2px solid rgba(211, 47, 47, 0.3);
  border-radius: 16px;
  padding: 20px;
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.4),
    inset 0 1px 0 rgba(211, 47, 47, 0.1);
}

.nav-button {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 16px 20px;
  background: rgba(42, 42, 42, 0.5);
  border: 2px solid transparent;
  border-radius: 12px;
  color: #cccccc;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: left;
  width: 100%;
}

.nav-button:hover {
  background: rgba(42, 42, 42, 0.8);
  border-color: rgba(211, 47, 47, 0.3);
  color: #ffffff;
  transform: translateX(4px);
}

.nav-button.active {
  background: linear-gradient(145deg, #d32f2f 0%, #b71c1c 100%);
  border-color: #d32f2f;
  color: #ffffff;
  box-shadow: 
    0 4px 12px rgba(211, 47, 47, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.nav-icon {
  font-size: 18px;
  width: 24px;
  text-align: center;
}

.nav-label {
  flex: 1;
}

.admin-main {
  flex: 1;
  background: linear-gradient(145deg, #2a2a2a 0%, #1a1a1a 100%);
  border: 2px solid rgba(211, 47, 47, 0.3);
  border-radius: 16px;
  padding: 30px;
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.4),
    inset 0 1px 0 rgba(211, 47, 47, 0.1);
}

/* Dashboard Styles */
.dashboard h2 {
  color: #ffffff;
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 30px;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.8);
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
}

.stat-card {
  background: linear-gradient(145deg, #333333 0%, #1e1e1e 100%);
  border: 2px solid rgba(211, 47, 47, 0.2);
  border-radius: 12px;
  padding: 24px;
  display: flex;
  align-items: center;
  gap: 16px;
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
}

.stat-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #d32f2f 0%, #ff6b6b 100%);
}

.stat-card:hover {
  transform: translateY(-4px);
  border-color: rgba(211, 47, 47, 0.4);
  box-shadow: 
    0 8px 25px rgba(0, 0, 0, 0.3),
    0 4px 12px rgba(211, 47, 47, 0.2);
}

.stat-icon {
  font-size: 32px;
  width: 50px;
  text-align: center;
}

.stat-info h3 {
  color: #d32f2f;
  font-size: 28px;
  font-weight: 900;
  margin-bottom: 4px;
  text-shadow: 0 0 10px rgba(211, 47, 47, 0.3);
}

.stat-info p {
  color: #cccccc;
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

/* View Main Site Link */
.view-main-site {
  position: fixed;
  top: 20px;
  right: 20px;
  background: linear-gradient(145deg, #d32f2f 0%, #b71c1c 100%);
  color: #ffffff;
  padding: 12px 20px;
  border-radius: 25px;
  text-decoration: none;
  font-weight: 600;
  font-size: 14px;
  box-shadow: 0 4px 12px rgba(211, 47, 47, 0.3);
  transition: all 0.3s ease;
  z-index: 1000;
  display: flex;
  align-items: center;
  gap: 8px;
}

.view-main-site:hover {
  background: linear-gradient(145deg, #f44336 0%, #d32f2f 100%);
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(211, 47, 47, 0.4);
  text-decoration: none;
  color: #ffffff;
}

/* Responsive Design */
@media (max-width: 1024px) {
  .admin-content {
    flex-direction: column;
  }
  
  .admin-nav {
    min-width: auto;
    width: 100%;
    flex-direction: row;
    overflow-x: auto;
    padding: 16px;
  }
  
  .nav-button {
    min-width: 200px;
    flex-shrink: 0;
  }
}

@media (max-width: 768px) {
  .admin-panel {
    padding: 20px 0;
  }
  
  .admin-header {
    padding: 20px;
    flex-direction: column;
    text-align: center;
  }
  
  .admin-user-info h1 {
    font-size: 24px;
  }
  
  .admin-main {
    padding: 20px;
  }
  
  .stats-grid {
    grid-template-columns: 1fr;
  }
  
  .view-main-site {
    position: relative;
    top: auto;
    right: auto;
    margin-bottom: 20px;
    align-self: center;
  }
}

@media (max-width: 480px) {
  .admin-nav {
    flex-direction: column;
  }
  
  .nav-button {
    min-width: auto;
  }
  
  .admin-header {
    padding: 16px;
  }
  
  .admin-main {
    padding: 16px;
  }
  
  .stat-card {
    flex-direction: column;
    text-align: center;
    gap: 12px;
  }
}
